<template>
	<view>
		<view class="cate" v-if="homeCate == 2">
			<HomeCateLY2 :list="homelist"></HomeCateLY2>
		</view>
		<view class="cate" v-if="homeCate == 3">
			<HomeCateLY3 :list="homelist"></HomeCateLY3>
		</view>
		<view class="cate" v-if="homeCate == 4">
			<HomeCateLY4 :list="homelist"></HomeCateLY4>
		</view>
		<view class="cate" v-if="homeCate == 5">
			<HomeCateLY5 :list="homelist"></HomeCateLY5>
		</view>
		<view class="cate" v-if="homeCate == 6">
			<HomeCate1 :list="homelist"></HomeCate1>
		</view>
		<view class="cate" v-if="homeCate == 7">
			<HomeCate2 :list="homelist"></HomeCate2>
		</view>
		<view class="cate" v-if="homeCate == 8">
			<HomeCate3 :list="homelist"></HomeCate3>
		</view>
		<view class="cate" v-if="homeCate == 9">
			<HomeCate4 :list="homelist"></HomeCate4>
		</view>
		<view class="cate" v-if="homeCate == 10">
			<HomeCate5 :list="homelist"></HomeCate5>
		</view>
		<view class="cate" v-if="homeCate == 11">
			<HomeCate6 :list="homelist"></HomeCate6>
		</view>
		<view class="cate" v-if="homeCate == 12">
			<HomeCate7 :list="homelist"></HomeCate7>
		</view>
		<view class="cate" v-if="homeCate == 13">
			<HomeCate8 :list="homelist"></HomeCate8>
		</view>
		<nav-bar :index="memu_id" :tabbar="true" :name="homelist.title"></nav-bar>
	</view>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from "vuex"
	// import mockdata from "./data"
	import HomeCate1 from "@/components/HomeCate1/HomeCate1.vue"
	import HomeCate2 from "@/components/HomeCate2/HomeCate2.vue"
	import HomeCate3 from "@/components/HomeCate3/HomeCate3.vue"
	import HomeCate4 from "@/components/HomeCate4/HomeCate4.vue"
	import HomeCate5 from "@/components/HomeCate5/HomeCate5.vue"
	import HomeCate6 from "@/components/HomeCate6/HomeCate6.vue"
	import HomeCate7 from "@/components/HomeCate7/HomeCate7.vue"
	import HomeCate8 from "@/components/HomeCate8/HomeCate8.vue"
	import HomeCateLY2 from "@/components/HomeCateLY2/HomeCateLY2.vue"
	import HomeCateLY3 from "@/components/HomeCateLY3/HomeCateLY3.vue"
	import HomeCateLY4 from "@/components/HomeCateLY4/HomeCateLY4.vue"
	import HomeCateLY5 from "@/components/HomeCateLY5/HomeCateLY5.vue"
	export default {
		data() {
			return {
				homelist: {
					navbar: [],
					news: [],
					notice: [],
					online: {},
					product: [],
					share: {},
					swiper: [],
				},
				memu_id: 1,
				cate: 1,
			};
			
		},
		computed: {
			...mapState(['theme', 'homeCate']),
		},
		components: {
			HomeCate1,
			HomeCate2,
			HomeCate3,
			HomeCate4,
			HomeCate5,
			HomeCate6,
			HomeCate7,
			HomeCate8,
			HomeCateLY2,
			HomeCateLY3,
			HomeCateLY4,
			HomeCateLY5,
		},
		onShareTimeline() {
			return {
				title: this.homelist.share.share_title,
				query: `login=${uni.getStorageSync('sharelogin')}&cate=${this.homeCate}`,
				// {
				// 	login: uni.getStorageSync('sharelogin'),
				// 	cate: this.homeCate
				// },
				imageUrl: this.homelist.share.image
			}
		},
		onShareAppMessage(res) {
			return {
				title: this.homelist.share.share_title,
				path: '/' + this.$scope.route + '?login=' + uni.getStorageSync('sharelogin'),
				imageUrl: this.homelist.share.image
			};
		},
		async onLoad(options) {
			// console.log(mockdata)
			if(options && options.cate) {
				this.$store.commit('setHomeCate', options.cate);
			}
			// this.homelist = mockdata.list
			uni.hideHomeButton()
			options.memu_id ? this.memu_id = options.memu_id : 1;
			options.login ? uni.setStorageSync('sharelogin', options.login) : {};
			await this.homeUrl();
		},
		onshow(options) {

		},
		methods: {
			...mapMutations(['changeVoteTheme']),
			async homeUrl() {
				// let cate = this.homeCate;
				// if(!cate) {
				// 	const res = await this.$http.get('home_cate')
				// 	if (res.data.code == 1) {
				// 		let pre_verson = 13 //容错还未更新模板的商家
				// 		const cate = res.data.cate > pre_verson ? 2 : res.data.cate
				// 		this.$store.commit('setHomeCate', cate);
				// 	}
				// }
				const res2 = await this.$http.get('xcx_home_data')
				if(res2.data.code == 1) {
					this.homelist = res2.data.list //mockdata.list
					uni.setStorageSync('home', this.homelist.share)
					uni.setNavigationBarTitle({
						title: this.homelist.share.title,
					});
				}
				
			},
		}
	}
</script>

<style>
	page {
		background-color: #f8f8f8;
	}
</style>

<style lang="scss" scoped>
	.cate1 {
		position: relative;
		padding: 20 0rpx;

		
	}
</style>
